// -----------------------------------------React-----------------------------------------
import React, { useState } from "react";
// -----------------------------------------Reactstrap-----------------------------------------
import {
	Container,
	Row,
	Col,
	Modal,
	ModalBody,
	ModalHeader,
	ModalFooter,
	Button
} from "reactstrap";

const FrontEndViewModal = ({ URL_HTML, URL_CSS, URL_JAVASCRIPT }) => {
	const [isOpen, toggle] = useState(false);
	const [view, setView] = useState("desktop");
	const toggleModal = () => {
		toggle(!isOpen);
	};
	const getView = () => {
		switch (view) {
			case "desktop":
				"xl";
			case "mobile":
				"sm";
			default:
				"xl";
		}
	};
	return (
		<div>
			<div className="view-now-button" onClick={toggleModal}>
				View Now
			</div>
			<Modal isOpen={isOpen} toggle={toggleModal} size={getView()}>
				<ModalBody>Test</ModalBody>
				<ModalFooter>Test</ModalFooter>
			</Modal>
		</div>
	);
};

export default FrontEndViewModal;
